.contenedor-sobreMi {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin: 7em auto 3em auto;
  padding-top: 5em;
  animation: revealOnScroll ease-in-out, desaparecerOnScroll ease-in-out;
  animation-timeline: view(), view();
  animation-range: cover 0% 50%, exit-crossing -10%;
}

.contenedor-sobreMi > .titulo {
  margin-bottom: 1em;
  padding-top: 1rem;
}

.presentacion {
  text-wrap: balance;
  font-size: 1.3em;
  text-align: center;
  padding: 1rem;
  line-height: 1.3;
  margin-bottom: 1em;
}

.contenedor-tablas {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0 1rem;
  padding: 0 1rem;
}

.tabla1, .tabla2 {
  padding: 1.5rem;
  margin-left: 0.5rem;
}

h3 {
  font-size: 1.3rem;
  margin-bottom: 1.2rem;
}

ul > li {
  list-style: none;
  padding: 0.5rem;
  font-size: 1.2em;
}

button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14rem;
  margin: 1rem;
  margin-top: 2rem;
  padding: 0.8rem;
  border-color: var(--negro);
  border-width: 0.2rem;
  border-radius: 1rem;
  background-color: var(--azul-3);
  color: var(--blanco);
  font-weight: 600;
  letter-spacing: 1px;
  cursor: pointer;
  font-size: 1.2rem;
}

.icono-boton {
  font-size: 1.6rem;
  margin-left: 0.3rem;
}

/* Para pantallas entre 800px y 1042px */
@media (min-width: 800px) and (max-width: 1042px) {

  .contenedor-tablas {
    margin: 0;
    padding: 0;
  }
  
  .tabla1, .tabla2 {
    padding: 1rem;
    margin-left: 0;
  }

  h3 {
    text-wrap: nowrap;
  }
  
  ul > li {
    list-style: none;
    padding: 0.4rem 0;
    font-size: 1.2em;
  }
}

/* Para pantallas menores a 800px */
@media (max-width: 799px) {

  .contenedor-sobreMi {
    width: 100%;
    margin-bottom: 1.2em;
    margin-top: 0;
    animation: none;
  }
  
  .contenedor-sobreMi .titulo {
    margin-bottom: 5px;
  }
  
  .contenedor-sobreMi .presentacion {
    font-size: 1.3em;
    text-align: center;
    padding: 1rem;
    line-height: 1.3;
    margin-bottom: 5px;
  }

  .contenedor-sobreMi .titulo {
    animation: revealOnScroll linear both, desaparecerOnScroll ease-in-out;
    animation-timeline: view(), view();
    animation-range: cover 10% 80%, exit-crossing -10%;
  }
  .contenedor-sobreMi .presentacion {
    animation: revealOnScroll linear both, desaparecerOnScroll ease-in-out;
    animation-timeline: view(), view();
    animation-range: entry-crossing 50% 70%, exit-crossing -20%;
  }
  
  .contenedor-tablas {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    margin-top: 2em;
    margin-bottom: 1em;
    padding: 0;
  }

  .tabla1 {
    margin-bottom: 1em;
  }
  
  .tabla1, .tabla2 {
    padding-left: 1em;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid var(--azul-4);
    border-radius: 5%;
    animation: revealOnScroll ease-in-out, desaparecerOnScroll ease-in-out;
    animation-timeline: view(), view();
    animation-range: cover 0% 30%, exit-crossing -10%;
  }
  
  h3 {
    font-size: 1.3rem;
    margin-bottom: 1.2rem;
    text-wrap: nowrap;
    
  }

  ul {
    padding-left: 1em;
  }
  
  ul li {
    list-style: none;
    padding: 0.5rem;
    font-size: 1.2em;
  }

  .contenedor-botones {
    flex-direction: column;
    margin-top: 1em;
    margin-bottom: 10em;
    animation: desaparecerOnScroll linear;
    animation-timeline: view();
    animation-range: exit-crossing -10%;
  }

  .contenedor-botones button {
    margin: 0.4em 0;
  }
}

@media (max-width: 280px) {

  div.contenedor-tablas {
    width: 100%;
  }
  .tabla1,
  .tabla2 {
    margin: 1rem 0;
    width: 90%;
  }
  .tabla1 h3,
  .tabla2 h3 {
    font-size: 1.1rem;
    padding:
  }
  .tabla1 li,
  .tabla2 li {
    font-size: 1rem;
  }
}

/*--------------- ANIMACIONES ---------------*/

@keyframes revealOnScroll {
  from {
    opacity: 0;
    transform: scale(0.7);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes desaparecerOnScroll {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.7);
  }
}

/*------------- ANIMACIONES ENDS --------------*/