.contenedor-inicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: calc(100vh - 70px); /*la altura de la barra de navegacion*/
  padding-top: 60px;
  color: var(--azul-4);
  animation: revealOnScroll ease-in-out, desaparecerOnScroll ease-in-out;
  animation-timeline: view(), view();
  animation-range: cover 0% 50%, exit-crossing -10%;
}

.imagen-perfil {
  width: 20rem;
  margin: 1rem;
  margin-top: 5rem;
  border: solid 2px var(--azul-4);
  border-radius: 45%;
  box-shadow: 0 0 5px var(--negro), 0 0 10px #000266, 0 0 15px var(--blanco);
  background-color: #695acd6a;
}

.texto-animado > .nombre {
  font-size: 2em;
  margin: 1.3rem 0 0.7rem 0;
  line-height: 1.3em;
  font-weight: bold;
  letter-spacing: 2px;
  animation: grow 2s both steps(14);
  overflow: hidden;
  text-wrap: nowrap;
  position: relative;
}

.texto-animado > .nombre::before {
  content: "";
  width: 2px;
  height: 100%;
  position: absolute;
  background-color: var(--negro);
  right: 0;
  animation: titilar 0.35s infinite alternate, desaparecer 0.1s both;
  animation-delay: 0s, 2.5s;
}

.texto-animado > .descripcion {
  font-size: 1.3em;
  font-weight: 400;
  animation: grow 3s both steps(35);
  letter-spacing: 1px;
  animation-delay: 2.5s;
  overflow: hidden;
  text-wrap: nowrap;
  position: relative;
}

.texto-animado > .descripcion::before {
  content: "";
  width: 2px;
  height: 100%;
  position: absolute;
  background-color: var(--negro);
  right: 0;
  animation: titilar 0.35s infinite alternate;
  animation-delay: 3.5s;
}

.contenedor-iconos {
  font-size: 1.7rem;
  margin: 0.7rem;
}

.icono-linkedin {
  margin-right: 3px;
  cursor: pointer;
}

.icono-github {
  margin-left: 3px;
  cursor: pointer;
}

.contenedor-iconos > a {
  text-decoration: none; /* Elimina el subrayado predeterminado */
}

.contenedor-inicio .contenedor-iconos svg {
  color: var(--azul-4);
  animation: aparecer 1s both;
  animation-delay: 6s;
}

.contenedor-iconos a:hover {
  transition: 0.3s;
  color: var(--azul-3);
}

/* Para pantallas menores a 800px */
@media (max-width: 799px) {
  .contenedor-inicio {
    height: 100vh;
    text-align: center;
    padding: 0 1rem;
    margin-bottom: 1em;
  }
  .imagen-perfil {
    margin-top: 0px;
    width: 280px;
  }
  .nombre {
    font-size: 1.9em;
    margin: 0.4em 0 1.2em 0;
  }
  .texto-animado > .descripcion {
    font-size: 1.2em;
    margin-bottom: 0.5em;
    text-wrap: wrap;
    letter-spacing: 0px;
    animation: aparecer 1.5s both;
    animation-delay: 2.5s;
    overflow: hidden;
  }
  .texto-animado > .descripcion::before {
    display: none;
  }
  .contenedor-inicio .contenedor-iconos svg {
    animation: aparecer 1s both;
    animation-delay: 3s;
  }
}

@media (max-width: 280px) {
  img.imagen-perfil {
    width: 90%;
  }
}

/*--------------- ANIMACIONES ---------------*/

@keyframes revealOnScroll {
  from {
    opacity: 0;
    transform: scale(0.7);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes desaparecerOnScroll {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.7);
  }
}

@keyframes grow {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

@keyframes titilar {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes desaparecer {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes aparecer {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/*------------- ANIMACIONES ENDS --------------*/
