.contenedor-portafolio {
  display: flex;
  flex-direction: column;
  color: var(--azul-4);
  justify-content: center;
  align-items: center;
  width: 80vw;
  margin-left: auto;
  margin-right: auto;
  padding-top: 4rem;
}

.contenedor-portafolio > .titulo {
  padding-top: 1em;
  animation: aparecerTitulo linear both;
  animation-timeline: view();
  animation-range: cover 30% cover 80%;
}

.contenedor-proyectos {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
  margin: auto;
  justify-content: center;
  width: auto;
  margin-bottom: 5rem;
}

.proyecto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: var(--amarillo);
  max-width: 30vw;
  height: 70vh;
  box-shadow: 2px 2px 2px 1px var(--azul-4);
  margin: 2rem 0;
  padding: 1rem 1rem;
  animation: revealOnScroll ease-in-out, desaparecerOnScroll ease-in-out;
  animation-timeline: view(), view();
  animation-range: cover 0% 50%, exit-crossing -20%;
}

.enlace {
  display: flex;
  justify-content: center;
}

.proyecto a {
  /* Estilos específicos para los enlaces dentro de proyectos */
  text-decoration: none;
  color: inherit;
}

.portada {
  width: 100%;
  height: 14rem;
  border: 2px solid #021d2c;
  max-width: 400px;
}

.proyecto video {
  object-fit: cover;
}

.descripcion-proyecto {
  text-align: center;
  text-wrap: balance;
  font-size: 1.2em;
  padding: 1.5rem 1rem;
  color: #021d2c;
}

.contenedor-lenguajes {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  width: 100%;
}

.icono {
  width: 3.5em;
  max-width: 3.5rem;
}

.contenedor-portafolio .contenedor-iconos {
  padding: 0.3rem 0;
  color: #021d2c;
}

.proyecto .icono-github {
  color: #021d2c;
}

@media screen and (min-width: 850px) and (max-width: 1100px) {

  .contenedor-proyectos {
    gap: 2rem;
  }
  .proyecto {
    width: 100%;
  }
}
@media screen and (max-width: 850px) {

  .contenedor-portafolio {
    margin-top: 0;
    width: 70%;
  }
  .titulo {
    text-align: center;
  }
  .contenedor-proyectos {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .proyecto {
    padding: 0;
    padding-top: 3em;
    max-width: 95%;
    height: auto;
  }
  .portada {
    width: 95%;
  }
  .descripcion-proyecto {
    padding-left: 0;
    padding-right: 0;
    font-size: 1.1em;
  }
  .contenedor-portafolio > .titulo {
    padding-top: 0;
    animation: aparecerTitulo linear both;
    animation-timeline: view();
    animation-range: cover 0% cover 80%;
  }
}
@media screen and (max-width: 450px) {
  .contenedor-portafolio {
    margin-top: 0;
    width: 90%;
  }
}
@media screen and (max-width: 280px) {

  .contenedor-portafolio {
    margin-top: 0;
    width: 100%;
  }
  .titulo {
    text-align: center;
  }
  .contenedor-proyectos {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
  }
  .proyecto {
    padding-top: 3em;
    max-width: 100%;
    height: auto;
  }
  .descripcion-proyecto {
    font-size: 1rem;
    padding-left: 0;
    padding-right: 0;
  }
  .icono {
    width: 3em;
    max-width: 3.5rem;
  }
}

/*--------------- ANIMACIONES ---------------*/

@keyframes revealOnScroll {
  from {
    opacity: 0;
    transform: scale(0.3);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes desaparecerOnScroll {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.3);
  }
}

@keyframes aparecerTitulo {
  from {
    opacity: 0;
    transform: scale(0.6);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/*------------- ANIMACIONES ENDS --------------*/